.sign-up {
  display: flex;
    align-items: center;
    gap: 20px;
    margin: 5%;
    justify-content: center;
    flex-direction: column;
}

.sign-in{
  display: flex;
    align-items: center;
    gap: 20px;
    margin: 5%;
    justify-content: center;
    flex-direction: column;
}

.form {
  box-shadow: rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px, rgba(17, 17, 26, 0.1) 0px 24px 80px;
  padding: 5%;
  border-radius: 20px;

  &__inputs {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-content: center;

    &__item {
      margin: 3px;
      background-color: $color-organic-green;
      border-radius: 20px;
      padding: 6px;
      border-color: cornsilk;
      color: antiquewhite;
      
      &::placeholder {
        color: #efe3d3;
        font-size: small;
      }
    }
  }
}
